.textarea {
  width: 50%;
  min-width: 370px;
  min-height: 78px;
  padding: 8px 10px;
  background: #fff;
  color: #666;
  font-size: 12px;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  outline: none;
}
.textarea:hover {
  border-color: #bfbfbf;
}
.textarea:hover:active,
.textarea:hover:focus {
  border-color: #00a0ff;
  box-shadow: 0px 0px 0px 2px rgba(0, 160, 255, 0.3);
}
.textarea:active,
.textarea:focus {
  border-color: #00a0ff;
  box-shadow: 0px 0px 0px 2px rgba(0, 160, 255, 0.3);
}
.textarea.error {
  border-color: #ff5959;
  box-shadow: 0px 0px 0px 2px rgba(255, 89, 89, 0.3);
}
.textarea.error:hover {
  border-color: #ff5959;
  box-shadow: 0px 0px 0px 2px rgba(255, 89, 89, 0.3);
}
.textarea.error:hover:active,
.textarea.error:hover:focus {
  border-color: #ff5959;
  box-shadow: 0px 0px 0px 2px rgba(255, 89, 89, 0.3);
}
.textarea.error:active,
.textarea.error:focus {
  border-color: #ff5959;
  box-shadow: 0px 0px 0px 2px rgba(255, 89, 89, 0.3);
}
textarea[disabled],
.textarea.disabled {
  background: #f2f2f2;
  color: #ccc;
  border-color: #e6e6e6;
  box-shadow: none;
  cursor: not-allowed;
}
textarea[disabled]:hover,
.textarea.disabled:hover {
  background: #f2f2f2;
  color: #ccc;
  border-color: #e6e6e6;
  box-shadow: none;
  cursor: not-allowed;
}
textarea[disabled]:hover:active,
.textarea.disabled:hover:active,
textarea[disabled]:hover:focus,
.textarea.disabled:hover:focus {
  background: #f2f2f2;
  color: #ccc;
  border-color: #e6e6e6;
  box-shadow: none;
  cursor: not-allowed;
}
textarea[disabled]:active,
.textarea.disabled:active,
textarea[disabled]:focus,
.textarea.disabled:focus {
  background: #f2f2f2;
  color: #ccc;
  border-color: #e6e6e6;
  box-shadow: none;
  cursor: not-allowed;
}
.limit-textarea p {
  color: #999;
  font-size: 12px;
}
.limit-textarea .words-error {
  color: #ff5959;
}



