.btn {
  display: inline-block;
  padding: 7px 20px;
  background-color: #00a0ff;
  background-image: none;
  font-size: 12px;
  color: #fff;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  border-radius: 4px;
  border: 1px solid transparent;
  cursor: pointer;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
  outline: none;
}
.btn:hover {
  background-color: #66c6ff;
}
.btn:focus,
.btn:active {
  background-color: #0090e6;
}
.btn.default {
  padding: 7px 20px;
}
.btn.large {
  padding: 8px 20px;
}
.btn.small {
  padding: 2px 10px;
}
.btn.primary {
  background-color: #00a0ff;
  color: #fff;
}
.btn.primary:hover {
  background-color: #66c6ff;
}
.btn.primary:focus,
.btn.primary:active {
  background-color: #0090e6;
}
.btn.secondary {
  background-color: #fff;
  color: #00a0ff;
  border-color: #66c6ff;
}
.btn.secondary:hover {
  background-color: #f2faff;
  color: #66c6ff;
  border-color: #66c6ff;
}
.btn.secondary:hover:focus,
.btn.secondary:hover:active {
  background-color: #f2faff;
  color: #0090e6;
  border-color: #0090e6;
}
.btn.secondary:focus,
.btn.secondary:active {
  background-color: #f2faff;
  color: #0090e6;
  border-color: #0090e6;
}
.btn.tertiary {
  background-color: #fff;
  color: #666;
  border-color: #d9d9d9;
}
.btn.tertiary:hover {
  background-color: #fff;
  color: #66c6ff;
  border-color: #66c6ff;
}
.btn.tertiary:hover:focus,
.btn.tertiary:hover:active {
  background-color: #fff;
  color: #0090e6;
  border-color: #0090e6;
}
.btn.tertiary:focus,
.btn.tertiary:active {
  background-color: #fff;
  color: #0090e6;
  border-color: #0090e6;
}
.btn.disabled {
  background-color: #f2f2f2;
  color: #ccc;
  cursor: not-allowed;
}
.btn.text {
  background-color: transparent;
  color: #666;
}
.btn.text:hover,
.btn.text:focus,
.btn.text:active {
  background-color: #f2faff;
}



