.input {
  width: 100%;
  min-width: 138px;
  padding: 6px 10px;
  background: #fff;
  color: #666;
  font-size: 12px;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  outline: none;
}
.input:hover {
  border-color: #bfbfbf;
}
.input:hover:active,
.input:hover:focus {
  border-color: #00a0ff;
  box-shadow: 0px 0px 0px 2px rgba(0, 160, 255, 0.3);
}
.input:active,
.input:focus {
  border-color: #00a0ff;
  box-shadow: 0px 0px 0px 2px rgba(0, 160, 255, 0.3);
}
.input.large {
  padding: 10px;
}
.input.small {
  padding: 4px 8px;
}
input[disabled],
.input.disabled {
  background: #f2f2f2;
  color: #ccc;
  border-color: #e6e6e6;
  box-shadow: none;
  cursor: not-allowed !important;
}
input[disabled]:hover,
.input.disabled:hover {
  background: #f2f2f2;
  color: #ccc;
  border-color: #e6e6e6;
  box-shadow: none;
  cursor: not-allowed !important;
}
input[disabled]:hover:active,
.input.disabled:hover:active,
input[disabled]:hover:focus,
.input.disabled:hover:focus {
  background: #f2f2f2;
  color: #ccc;
  border-color: #e6e6e6;
  box-shadow: none;
  cursor: not-allowed !important;
}
input[disabled]:active,
.input.disabled:active,
input[disabled]:focus,
.input.disabled:focus {
  background: #f2f2f2;
  color: #ccc;
  border-color: #e6e6e6;
  box-shadow: none;
  cursor: not-allowed !important;
}
.input.error {
  border-color: #ff5959;
  box-shadow: 0px 0px 0px 2px rgba(255, 89, 89, 0.3);
}
.input.error:hover {
  border-color: #ff5959;
  box-shadow: 0px 0px 0px 2px rgba(255, 89, 89, 0.3);
}
.input.error:hover:active,
.input.error:hover:focus {
  border-color: #ff5959;
  box-shadow: 0px 0px 0px 2px rgba(255, 89, 89, 0.3);
}
.input.error:active,
.input.error:focus {
  border-color: #ff5959;
  box-shadow: 0px 0px 0px 2px rgba(255, 89, 89, 0.3);
}
.input.success {
  border-color: #1dbf38;
  box-shadow: 0px 0px 0px 2px rgba(29, 191, 56, 0.3);
}
.input.success:hover {
  border-color: #1dbf38;
  box-shadow: 0px 0px 0px 2px rgba(29, 191, 56, 0.3);
}
.input.success:hover:active,
.input.success:hover:focus {
  border-color: #1dbf38;
  box-shadow: 0px 0px 0px 2px rgba(29, 191, 56, 0.3);
}
.input.success:focus,
.input.success:active,
.input.success:focus {
  border-color: #1dbf38;
  box-shadow: 0px 0px 0px 2px rgba(29, 191, 56, 0.3);
}



